.feedback {
  background-color: rgb(255, 255, 255);
}

.feedback__container {
  position: relative;
  padding-bottom: 50px;
}

.feedback__container::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;

  display: block;
  width: 100%;
  height: 6px;

  background-image: url("../img/bg-zigzag-line.svg");
  background-repeat: repeat-x;
  background-position: bottom;
  background-size: contain;
}


.feedback__quote {
  margin: 0;
  padding: 0;

  line-height: 30px;
}

.feedback__author {
  display: block;
  margin-top: 25px;
  font-size: 17px;
  line-height: 30px;
  font-style: normal;
  color: rgb(0, 0, 0);

  & b {
    font-weight: 700;
  }
}

.feedback__channel {
  display: block;

  font-style: normal;
  line-height: 30px;
}

.button__backward {
  background-image: url("../img/icon-left-arrow.svg");
}

.button__forward {
  background-image: url("../img/icon-right-arrow.svg");
}

@media (min-width: $tablet-width) {
  .feedback {
    position: relative;
  }

  .feedback::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;

    display: block;
    width: 100%;
    height: 15px;

    background-image: url("../img/bg-zigzag-line.svg");
    background-repeat: repeat-x;
    background-position: bottom;
    background-size: contain;
  }

  .feedback__container {
    z-index: 2;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 80px;
    padding-bottom: 95px;

    background-image: url("../img/review@1x.jpg");
    background-repeat: no-repeat;

    @media (min-resolution: $retina-dpi),
      (min-resolution: $retina-dppx) {
      background-image: url("../img/review@2x.jpg");
      background-size: 350px auto;
    }
  }

  .feedback__container::after {
    display: none;
  }

  .feedback__title {
    width: 50%;
    padding-bottom: 0;
  }

  .feedback__quote {
    order: 3;
    width: 100%;
    margin-top: 45px;
    margin-bottom: 32px;

    font-size: 17px;
  }

  .feedback__cite {
    order: 4;
    width: 50%;
  }

  .feedback__author {
    font-size: 18px;
    line-height: 28px;
  }

  .feedback__toggles {
    order: 5;
    width: 50%;
  }

  .feedback__apply {
    order: 2;
    width: auto;
    height: 52px;
    padding: 14px 38px;
  }
}

@media (min-width: $desktop-width) {
  .feedback {
    position: static;
  }

  .feedback::after {
    display: none;
  }

  .feedback__container {
    padding-left: 225px;

    background-position-x: 50px;
  }

  .feedback__quote {
    max-width: 88%;
    margin-top: 40px;
    margin-bottom: 40px;
  }

  .feedback__author {
    margin: 0;
  }
}
